// siehe https://vuelidate.js.org/#getting-started

export const formValidation = {
  methods: {
    // Fr das ausgwehlte Formular-Objekt (z.B. Nutzername, Vorname, Passwort, ...) wird geprft,
    // ob die gewnschten Anforderungen an die Eingabe erfllt sind
    validateState(name) {
      const { $dirty, $error } = this.$v.form[name];
      return $dirty ? !$error : null;
    },

    // Wird ausgefhrt, um ein Formular abzuschicken
    // Sind die alle gewnschten Anforderungen an das Formular erfllt, dann abschicken
    validateSubmit(event) {
      event.preventDefault()
      // Entscheide die Korrektheit der Eingabe fr alle "unberhrten" Felder
      this.$v.form.$touch()
      return !this.$v.form.$anyError
    },

    onReset(event) {
      event.preventDefault()
    },
  }
}